<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top">
				<div class="search_box">
					<el-select v-model="subscribe_fund" placeholder="请选择认购基金" clearable filterable>
					    <el-option-group v-for="group in allSubscriptionFundList" :key="group.label" :label="group.label"> 
							<el-option v-for="item in group.options" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-option-group>
					</el-select>
				</div>
			</div>
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-input v-model="fund_name" placeholder="子基金全称" clearable>
					</el-input>
				</div>
				<div class="btn_box">
					<button @click="getInitData(1)">搜索</button>
				</div>
			</div>
		</div>
		<div class="table_title flex_between">
			<div class="">
				子基金认购基金组合总数<span>(&nbsp;{{ total }}&nbsp;)</span>
			</div>
			<div class="flex_end">
				<button class="blue_btn flex_center" @click="downloadInfo">
					<span class="iconfont icon-daoru"></span>
					导出excel表格
				</button>
			</div>
		</div>
		<div class="table">
			<el-table :data="tableData" border style="width: 100%">
				<el-table-column sortable prop="id" label="序号" width="80">
					<template slot-scope="scope">
						{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="shortname" label="子基金简称" width="200">
				</el-table-column>
				<el-table-column sortable prop="fund_id" label="ID" width="80">
				</el-table-column>
				
				<el-table-column sortable prop="name" label="子基金全称" width="200">
				</el-table-column>
				<el-table-column sortable prop="number" label="编号" width="80">
				</el-table-column>
				<el-table-column sortable prop="subscribeshortname" label="认购基金简称" width="280">
				</el-table-column>
				<el-table-column sortable prop="subscribename" label="认购基金全称" width="280">
				</el-table-column>
				
				<el-table-column sortable prop="subscribe_fee" label="认缴（万元）" width="150">
				</el-table-column>
				<el-table-column sortable prop="real_fee" label="实缴（万元）" width="150">
				</el-table-column>
				<el-table-column sortable prop="real_rate" label="实缴进度" width="150">
					<template slot-scope="scope">
						{{ scope.row.real_rate }}&nbsp;%
					</template>
				</el-table-column>
				<el-table-column sortable prop="multiple" label="倍数" width="120">
				</el-table-column>
				<el-table-column sortable prop="irr" label="XIRR" width="120">
				</el-table-column>
				<el-table-column sortable prop="back" label="回款返本（万元）" width="180">
				</el-table-column>
				<el-table-column sortable prop="income" label="回款收益-税后（万元）" width="200">
				</el-table-column>
				<template slot="empty">
					<div class="table-nodata flex_start">
						<img src="../../images/table_nodata.png">
						<span>没有对应数据</span> 
					</div>
				</template>
			</el-table>
			<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import { getSubscriptionFundListApi,subscribeListReportSubApi,subscribeDownloadInfoReportSubApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	
	export default {
		name: 'subFundSubscriptTotal',
		data() {
			return {
				subscribe_fund: '',
				fund_name: '',

				allSubscriptionFundList: [
					{
						label: '母基金基金',
						options: [ ]
					},
					{
						label: '自管基金',
						options: [ ]
					}
				],
				
				currentPage: 1,
				pageSize: 10,
				total: 0,
				tableData: [ ]
			}
		},
		created() {
			this.getSubscriptionFundList()
			this.getInitData()
		},
		mounted() {},
		components: {},
		methods: {
			getInitData(is_init_page){
				if (is_init_page) {
					this.currentPage = 1;
				}
				subscribeListReportSubApi({
					subscribe_id: this.subscribe_fund,
					chidfund: this.fund_name,
					page: this.currentPage,
					pageSize: this.pageSize
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.total = res.data.payload.total;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取子基金认购基金列表
			getSubscriptionFundList(){
				getSubscriptionFundListApi({

				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.allSubscriptionFundList[0].options = Object.assign({}, res.data.payload.fof_fund);
						this.allSubscriptionFundList[1].options = Object.assign({}, res.data.payload.self_fund);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadInfo(){
				subscribeDownloadInfoReportSubApi({

				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						var path = res.data.payload.path;
						window.location.href = path;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInitData()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInitData()
			},
		},
	}
</script>

<style scoped="scoped">
	.body{
		padding: 0 !important;
	}
	.search_group{
		padding: 30px 0;
    		border-bottom: 1px solid #F1F1F1;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.date_box{
		width: 162px;
	}
	.date_middle_text{
		margin: 0 10px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	.table_title{
		margin: 26px 0;
		color: #6292F4;
		font-size: 14px;
	}
	.table_title span{
		color: #6292F4;
	}
	.table_title button{
		margin-left: 10px;
	}
</style>


